import React from 'react';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import { Link } from 'react-router-dom';

import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import IconButton from '@material-ui/core/IconButton';
import StarBorderIcon from '@material-ui/icons/StarBorder';
import image1 from '../assets/images/ColorSwatches.png';
import image2 from '../assets/images/ColorPalette.png';
import image3 from '../assets/images/ColorRules.png';

const tileData = [
     {
       img: image1,
       title: 'Image',
       author: 'author',
     },
     {
      img: image2,
      title: 'Image',
      author: 'author',
    },
    {
      img: image3,
      title: 'Image',
      author: 'author',
    },
   ];

const useStyles = makeStyles((theme) => ({
  root:{
    backgroundColor: '#fff',
    color: theme.palette.bgBlack.main,
    minHeight: '90vh',
    fontSize: '25px',
  },
  linkBtn: {
    textDecoration: 'none',
  },  
  spacer: {
    height: '80px',
  },
  whiteColumn: {
    backgroundColor: '#fff',
  },
  heroSection: {
    backgroundColor: theme.palette.bgLightBlue.main,
    position: 'relative',
  },
  heroTitle: {
    position: 'absolute',
    top: -30,
  },
  gridRoot: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
  },
  gridList: {
    flexWrap: 'nowrap',
    // Promote the list into his own layer on Chrome. This cost memory but helps keeping high FPS.
    transform: 'translateZ(0)',
  },
  title: {
    color: theme.palette.primary.light,
  },
  titleBar: {
    background:
      'linear-gradient(to top, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)',
  },
}));

function CaseStudies() {
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <div className={classes.spacer}></div>
      {/*Main content grid container*/}
      <Grid container direction='column'>
        <Grid item container className={classes.heroSection}>
          <Grid item xs={1} sm={2} xl={3} className={classes.whiteColumn}/>
          <Grid item xs={10} sm={8} xl={6}>
            <Grid item container spacing={2}>
            <Grid item md={9} sm={12}>
            <Typography gutterBottom variant="h1" className={classes.heroTitle}>
                 Protfolio Website
                </Typography>
                <div className={classes.spacer}></div>
                <Typography variant="body1">
                  Since graduating and building my first portfolio website, every few years 
                  I like to take the time to update and redesign my website. This is a chance 
                  for me to test new ideas, experiment with styles, and utilize new technologies. 
                  In this redesign my goals were:
                  <ul>
                    <li>
                    Inform users of who I am and what my skills are within 30 seconds of 
                    visiting my website.
                    </li>
                    <li>
                    Learn new libraries (Material-ui, React-spring, Formik) and implement them.
                    </li>
                  </ul>
                </Typography>
                <div className={classes.spacer}></div>  
            </Grid>
            <Grid item md={3} sm={12}/>
            
            </Grid>
          </Grid>
          <Grid item xs={1} sm={2} xl={3}/>
          
        </Grid>{/**End of Portfolio Website section */}   

        <Grid item container className={classes.heroSection}>
          <Grid item xs={1} sm={2} xl={3}/>
          <Grid item xs={10} sm={8} xl={6}>
            <Grid container spacing={2}>
            <Grid item xs={12}>
            {/* <div className={classes.gridRoot}>
      <GridList className={classes.gridList} cols={2.5}>
        {tileData.map((tile) => (
          <GridListTile key={tile.img}>
            <img src={tile.img} alt={tile.title} />
            <GridListTileBar
              title={tile.title}
              classes={{
                root: classes.titleBar,
                title: classes.title,
              }}
              actionIcon={
                <IconButton aria-label={`star ${tile.title}`}>
                  <StarBorderIcon className={classes.title} />
                </IconButton>
              }
            />
          </GridListTile>
        ))}
      </GridList>
    </div> */}
            </Grid>
            </Grid>
          </Grid>
          <Grid item xs={1} sm={2} xl={3}/>
        </Grid>

        <div className={classes.spacer}></div>
        <Grid item container>
          <Grid item xs={1} sm={2} xl={3}/>
          <Grid item xs={10} sm={8} xl={6}>
            <Grid container spacing={2}>
            <Grid item xs={12}>
            <Grid container direction='column' alignItems='center'>
            <Grid item xs>
            <Typography variant="body1">
              Have questions? I'd love to hear from you.               
            </Typography>
            </Grid>
            <Grid item xs>
            <Link to='/home/contact' className={classes.linkBtn}>
                      <Button size="large" variant="contained" color="primary">
                        Let's Talk!
                      </Button>
                    </Link>
            </Grid>
            </Grid>
            </Grid>
            </Grid>
          </Grid>
          <Grid item xs={1} sm={2} xl={3}/>
        </Grid>{/**End of Contact button section */}

      </Grid>{/*End of main container*/}
      
      <div className={classes.spacer}></div>
    </div>
  );
};

export default CaseStudies;
